<template>
  <v-container>
    <v-data-table
      class="products-table elevation-1 mt-0"
      :headers="tableHeaders"
      :items="basketItems"
      :items-per-page="-1"
      :mobile-breakpoint="0"
      :group-by="[{ key: 'vendorId' }]"
      item-value="id"
      disable-sort
    >
      <template #group-header="{ item, columns, toggleGroup, isGroupOpen }">
        <template :ref="() => { if (!isGroupOpen(item) && !loaded) toggleGroup(item) }"></template>
        <tr class="v-row-group__header">
          <td :colspan="columns.length - 1">
            <v-layout class="products-table__vendor">
              <v-icon class="pr-2">mdi-chevron-right</v-icon>{{ getVendorName(item.items) }}
            </v-layout>
          </td>
        </tr>
      </template>

      <template #item.title="{ item }">
        <span>{{ item.title }}</span>
      </template>

      <template #item.quantity="{ item }">
        <v-btn
          icon="mdi-plus"
          variant="text"
          size="small"
          @click="changeQuantity(item, 'add')"
        />
        <v-chip label>{{ item.quantity }}</v-chip>
        <v-btn
          icon="mdi-minus"
          variant="text"
          size="small"
          @click="changeQuantity(item, 'remove')"
        />
      </template>

      <template #item.price="{ item }">
        <v-chip color="secondary">${{ item.price }}</v-chip>
      </template>

      <template #item.total="{ item }">
        <v-chip color="primary">${{ getTotalPriceForItem(item) }}</v-chip>
      </template>

      <template #item.actions="{ item }">
        <v-btn
          icon="mdi-trash-can-outline"
          color="error"
          variant="text"
          @click="removeFromBasket(item)"
        ></v-btn>
      </template>

      <template #body.append="{ columns }">
        <tr class="v-data-table__tr">
          <td v-for="(column, idx) in columns" :key="column.text">
            <div v-if="idx === 0">
              <strong>Total</strong>
            </div>

            <v-chip v-if="column.key === 'total'" color="primary" label>
              ${{ totalPrice }}
            </v-chip>
          </td>
        </tr>
      </template>
    </v-data-table>
  </v-container>
</template>

<script>
import { ref, computed, onMounted } from "vue";

import { useOrdersStore } from "@/stores/ordersStore";

export default {
  name: "OrdersTable",
  setup() {
    const basketItems = computed(() => useOrdersStore().basket);

    const loaded = ref(false)

    const tableHeaders = ref([
      { title: "Product", value: "title" },
      { title: "Quantity", value: "quantity" },
      { title: "Price", value: "price" },
      { title: "Total Price", value: "total" },
      { title: "", value: "actions" },
    ]);

    const removeFromBasket = (item) => {
      useOrdersStore().removeFromBasket(item.id);
    };

    const totalPrice = computed(() => {
      return basketItems.value.reduce(
        (acc, item) => acc + getTotalPriceForItem(item),
        0
      );
    });

    const getTotalPriceForItem = (item) => {
      return item.price * (item?.quantity || 1);
    };

    const changeQuantity = (item, action = "add") => {
      const { quantity, id } = item;
      const newQuantity = action === "add" ? quantity + 1 : quantity - 1;
      if (newQuantity < 1) return;
      useOrdersStore().updateProductQuantity(id, newQuantity);
    };

    const getVendorName = (items) => {
       return items[0].raw?.vendorName
    };

    onMounted(() => {
      loaded.value = true
    })

    return {
      basketItems,
      tableHeaders,
      totalPrice,
      loaded,
      getVendorName,
      removeFromBasket,
      changeQuantity,
      getTotalPriceForItem,
    };
  },
};
</script>

<style lang="scss">
.products-table {
  .v-data-table-header__content {
    font-weight: bold;
  }

  .v-data-table__tr td:first-child,
  thead tr th:first-child {
    display: none;
  }

  .v-row-group__header {
    background: #f5f5f5;
    font-weight: bold;
  }
}
</style>
