<template>
  <v-container>
    <v-breadcrumbs :items="breadcrumbs">
      <template #divider>
        <v-icon icon="mdi-chevron-right"></v-icon>
      </template>
    </v-breadcrumbs>

    <selected-products-table />

    <v-container>
      <v-card>
        <v-card-title>Summary</v-card-title>
        <v-card-text>
          <v-form ref="form" v-model="isValid">
            <v-text-field
              label="Name"
              v-model="formData.name"
              :rules="[rules.required]"
            />

            <v-text-field
              label="Email"
              v-model="formData.email"
              :rules="[rules.required, rules.email]"
            />

            <v-text-field
              label="Address"
              v-model="formData.address"
              :rules="[rules.required]"
            />

            <v-text-field
              label="Phone Number"
              v-model="formData.phone"
              :rules="[rules.required, rules.phone]"
            />

            <v-textarea
              label="Message"
              v-model="formData.message"
              auto-grow
              :rules="[rules.maxLength(500)]"
            />

            <v-btn :disabled="!isValid" @click="handleSubmit" color="primary">
              Submit Order
            </v-btn>
          </v-form>
        </v-card-text>
      </v-card>
    </v-container>
  </v-container>
</template>

<script>
import { ref } from "vue";

import { validationRules as rules } from "@/utils";

import SelectedProductsTable from "@/components/basket/SelectedProductsTable.vue";

export default {
  components: {
    SelectedProductsTable,
  },
  setup() {
    const breadcrumbs = [
      {
        title: "Home",
        disabled: false,
        href: "/",
      },
      {
        title: "Basket",
        disabled: true,
      },
    ];

    const isValid = ref(false);

    const formData = ref({
      name: null,
      email: null,
      address: null,
      phone: null,
      message: null,
    });

    const handleSubmit = () => {
      if (isValid.value) {
        alert(`Order Submitted: ${JSON.stringify(formData.value)}`);
      }
    };

    return {
      breadcrumbs,
      rules,
      isValid,
      formData,
      handleSubmit,
    };
  },
};
</script>
